.review-detail{
    display: grid;
    grid-template-columns: [title-s] 1fr [title-e]  65px [price-s] 1fr [price-e];
    grid-template-rows: 1fr;
    align-items: center;

    &__title{
        grid-column: title-s/title-e;
        grid-row: 1/-1;
        text-align: right;
        font-size: 14px;
    }
    &__price{
        grid-column: price-s/price-e;
        grid-row: 1/-1;
        font-size: 18px;
        font-weight: 600;
    }
}

.review-detail-green{
    
    margin-top: 18px;

    &__title,
    &__price{
        color: #00cb79;
        font-weight: 600;
    }
}