.sign-in{
    grid-row: content-s/content-e;
    grid-column: s/e;

    // &__page-title {
        
    // }

    &__form {
        display: grid;
        grid-template-rows: 
        [email-s]
        64px
        [email-e password-s]
        64px
        [password-e]
        minmax(10px, 289px)
        [line-s]
        2px
        [line-e login-s]
        38px
        [login-e];
        grid-template-columns: 1fr;
    }
}

.sign-in-form {
        & > * {
            grid-column: 1/-1;
        }
        &__email {
            grid-row: email-s/email-e;
        }

        &__password {
            grid-row: password-s/password-e;
            margin-top: 15px;
        }

        &__line {
            grid-row: line-s/line-e;
            border-top: 1px solid #ccc;
        }

        &__login {
            margin-top: 39px;
            grid-row: login-s/login-e;


        }
        &__details {
            grid-row: 1/-1;
            justify-self: end;
            align-self: start;
        }
}